export default class Vector3D {
  constructor({ x = 0, y = 0, z = 0, magnitude = 1 } = {}) {
    this.x = x;
    this.y = y;
    this.z = z;
    this.magnitude = magnitude;

    return this;
  }


  /**
   * Sets the x and y position at the same time
   *
   * @param {Number} x
   * @param {Number} y
   */
  set(x, y, z) {
    this.x = x;
    this.y = y;
    this.z = z;

    return this;
  }


  /**
   * Sets the magnitude of the vector.
   * This is like how 'far' the vector moves
   * each frame
   *
   * @param {Number} magnitude
   */
  setMag(magnitude) {
    this.normalize();
    this.magnitude = magnitude;

    return this.mult(this.magnitude);
  }


  /**
   * Returns the vector to a unit of 1
   */
  normalize() {
    return this.mult(1 / this.magnitude);
  }


  /**
   * Multiplies the vector uniformly
   * by a number
   *
   * @param {Number} num
   */
  mult(num) {
    this.x *= num;
    this.y *= num;
    this.z *= num;

    return this;
  }


  /**
   * Divides the vector uniformly
   * by a number
   *
   * @param {Number} num
   */
  div(num) {
    this.x /= num;
    this.y /= num;
    this.z /= num;

    return this;
  }


  /**
   * Subtracts a vector like object
   *
   * @param {object} Vector
   */
  sub({ x, y, z }) {
    this.x -= x;
    this.y -= y;
    this.z -= z;

    return this;
  }


  /**
   * Adds a vector like object
   *
   * @param {object} Vector
   */
  add({ x, y, z }) {
    this.x += x;
    this.y += y;
    this.z += z;

    return this;
  }


  /**
   * Returns dot product of 2 vectors
   *
   * @param {object} Vector
   */
  dot({ x, y, z }) {
    return this.x * x + this.y * y + this.z * z;
  }


  /**
   * Gets distance between 2 vectors
   *
   * @param {object} Vector
   */
  getDistance({ x, y, z }) {
    let a = this.x - x;
    let b = this.y - y;
    let c = this.z - z;
    return Math.sqrt(a * a + b * b + c * c);
  }

  /**
   * Returns a new vector from current
   * settings
   */
  clone() {
    return new Vector3D({
      x: this.x,
      y: this.y,
      z: this.z,
      magnitude: this.magnitude
    });
  }
}
